.cart {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.cartItem {
  width: 700px;
  padding: 1rem 0 1rem 0;
  display: flex;
  align-items: center;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  border-radius: 25px;
  margin: 30px 0 30px 0;
}

.cartItem img {
  width: 200px;
  margin-right: 2rem;
}

.cartItem .description {
  width: 100%;
  font-size: 30px;
}

.itemHandler {
  display: flex;
  width: 20rem;
  justify-content: space-between;
}

.removeAll {
  color: white;
  background-color: rgb(19, 19, 19);
  border-radius: 3px;
  border: none;
  cursor: pointer;
  font-size: 1.5rem;
  padding: 0.5rem;
}

.countHandler {
  display: flex;
  gap: 0.3rem;
}

.countHandler button {
  color: white;
  background-color: rgb(19, 19, 19);
  border-radius: 3px;
  border: none;
  cursor: pointer;
  width: 2.5rem;
  font-size: 1.5rem;
}

.countHandler input {
  width: 40px;
  text-align: center;
  font-weight: bolder;
  font-size: 1.3rem;
}

.checkout {
  display: flex;
  flex-direction: column;
  width: 25rem;
}

.checkout p {
  font-size: 1.5rem;
}

.checkout-inner-flex {
  display: flex;
  justify-content: space-between;
}

.checkout-inner-flex button {
  width: 150px;
  height: 60px;
  background-color: rgb(19, 19, 19);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 0.8rem;
  font-size: 1.2rem;
  cursor: pointer;
}

@media (max-width: 800px) {
  .cartItem {
    width: 350px;
  }
  .itemHandler {
    flex-direction: column;
    width: 100%;
    align-items: center;
    gap: 1rem;
  }
  .removeAll {
    width: 70%;
  }
  .checkout {
    width: 20rem;
  }
  .cartItem img {
    margin-right: 0.3rem;
  }
}

@media (max-width: 520px) {
  .checkout {
    align-items: center;
    width: 15rem;
  }
  .countHandler button, .removeAll {
    font-size: 1rem;
  }
  .countHandler button {
    width: 2rem;
  }
  .checkout-inner-flex {
    flex-direction: column;
    gap: 2rem
  }
  .cartItem .description {
    font-size: 20px;
  }
}

@media (max-width: 300px) {
  .cartItem {
    width: 250px;
  }
  .cartItem img {
    width: 80px;
    margin-right: 1.5rem;
  }
  .checkout {
    width: 15rem;
  }
}
